<script>
import axios from 'axios'
import Content from "./Content.vue";


// const cityOptions = ['Excel', '3Dsmax', 'Photoshop'];
export default {
  props:['mesRj','mesGame','mesMx','mesAge'],
  name:'AtriclCotent',
  components:{
