  },
  data() {
      return {
        form: {
          name: 'http://124.71.73.195:7766/mt',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        checkedCities: [],
        // cities: cityOptions,
        input:'',
        name:'',
        //fc判断号码是否为空
        fc:false,
        //fck判断是否’年龄为必填项‘
        fck:false,  
        fckArticl:'年龄为必填项，请选择',
        articl:'',
        // datastr:[]
        datastr:'',
        success:'提交'      
      };
  },

    methods: {
      onSubmit() {
        console.log('submit!');
      },
      st (){
       flg = false
      },
      numCheck () {
        let regs = /^((13[0-9])|(17[0-1,6-8])|(15[^4,\\D])|(18[0-9]))\d{8}$/;
          if(this.input.length != 0){
            if(!regs.test(this.input)){
                // console.log('手机号输入不合法');
                this.articl = "手机号格式错误"
                this.fc = true
            }else {
                this.fc = false
            }
          }else {
            this.articl = "手机号不能为空"
            this.fc = true
          }
      },
      async ax () {
        // async function () {
          console.log("456");
          const { data: res } = await axios({
            url: 'http://124.71.73.195:7766/mt',
            method: 'post',
            headers : {
              'Content-type': 'application/json'
            },
            data: {
              "formStr":"Excel,绝地求生,人物建模,17岁以下",
              "phone":"13798171621",
              "empName":"yhy"
            }
          })
          console.log(res.data)
      },
      async postInfo(){
        if(this.input === ''){
            this.articl = "手机号不能为空"
            this.fc = true
        }
       
        let age = this.$el.querySelector('#age').getElementsByTagName('label')
        let flg = false
        for(let i =0;i<age.length;i++){
          // console.log(age[i].getElementsByTagName('span')[0].classList)
          if(age[i].getElementsByTagName('span')[0].classList.length ==2 ){
            // console.log(age[i].getElementsByTagName('span')[2].outerText);
            var newFormStr = this.datastr + age[i].getElementsByTagName('span')[2].outerText
            flg = true
            this.fck = false
            this.fckArticl = ''
            break
          }else {
          this.fckArticl = '年龄为必填项，请选择'
          }
        }
        // console.log(this.datastr.slice(0,-1))
        // console.log(newFormStr);
        if (flg && this.input !== ''){
          const {data:res} =await axios.post('http://124.71.73.195:7766/mt',{
              "formStr": newFormStr,
              // "formStr":"Excel,绝地求生,人物建模,17岁以下",
              "phone":this.input,
              "empName":this.name,
          })
            

          if (res.code == 1){
            console.log("456");
            this.success = '提交成功'
          }else {
            this.success = '提交失败'
          }
        }else {
          this.fck = true
          // console.log('年龄为必填项');
        }
        
      },
      datachange (val){
        this.datastr += val +',' 
      },   
    }
}
