<template>
  <div v-if="flag">
    <!-- <p>{{rjmes}}</p> -->
    <slot name="title" class="pt"></slot>
    <el-checkbox-group 
      v-model="checkedCities"
      @change="changeCheckbox"
      >
      <el-checkbox v-for="rj in mes" :label="rj" :key="rj">{{rj}}</el-checkbox>
    </el-checkbox-group>
  </div>
  <div v-else>
    <slot name="title" class="pt"></slot>
      <el-checkbox-group 
        v-model="checkedCities"
        @change="changeCheckbox"
        >
        <el-checkbox v-for="mx in mes" :label="mx" :key="mx" style="width: 140px; margin-bottom: 10px;">{{mx}}</el-checkbox>
      </el-checkbox-group>

      <div class="demo-image__placeholder">
        <div class="block">
          <el-image :src="require('../assets/选项_人物建模.gif')" style="width: 130px; height: 80px;margin-right: 40px;"></el-image>
          <el-image :src="require('../assets/选项_道具建模.gif')" style="width: 130px; height: 80px;margin-right: 40px;"></el-image>
          <el-image :src="require('../assets/选项_场景建模.gif')" style="width: 130px; height: 80px"></el-image>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  name:'Content',
  props:["rjmes","flag"],
  data() {
      return {
          checkedCities: [],
          mes:this.rjmes,
          dataStr:[]
      }
  },
  methods:{
    changeCheckbox(val){
      // console.log(val)
      this.checkedCities = this.checkedCities.slice(-1)
      this.$emit('datStr',val)
      // console.log(this.checkedCities);
      
      
    }
  }
  
}
</script>

